<template>
    <div class="ui-container">
        <div class="amimate-item">
            <div class="am-content mu-player">
                <!-- <div class="amimate-close" @click="toLister"><a>返回</a></div> -->
                <div class="play-main">
                    <div class="play-title">
                        <p class="na">听妈妈的话</p>
                        <p class="de">周杰伦</p>
                    </div>
                    <div class="play-cd">
                        <span class="x-img"><img :src="musicListData[index].img" class="image-cd"></span>
                    </div>
                    <div class="play-progress">
                        <span>04:30</span>
                        <div class="progress-bar"><i></i></div>
                        <span>04:30</span>
                    </div>
                    <div class="play-control">
                        <span class="btn-prev"></span>
                        <span class="btn-play"></span>
                        <span class="btn-next"></span>
                    </div>
                </div>
                <img :src="musicListData[index].img" class="ms">
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data () {
        return {
            index: 1
        }
    },
    created () {
        this.index = this.$route.query.id
    },
    computed: {
        musicListData () {
            return this.$store.state.list.list
        }
    },
    methods: {
        toLister () {
            this.$router.go(-1)
        }
    }
}
</script>

<style lang="scss">
@import '../assets/style/global.scss';
.tabs {
    display: flex;
    a {
        flex: 1;
        text-align: center;
        line-height: 50px;
        &.on {
            color: #f00;
        }
    }
}
.mu-player {
    overflow: hidden;
    display: flex;
    img.ms {
        position: absolute;
        z-index: 0;
        top: -50%;
        left: -50%;
        z-index: 3;
        height: 200%;
        object-fit: cover;
        transform: scale(1.1) translateZ(0);
        filter: blur(36px);
    }
    .play-main {
        flex: 1;
        display: flex;
        flex-direction: column;
        position: relative;
        z-index: 10;
    }
    .play-cd {
        display: flex;
        flex: 1;
        padding: 20px;
        align-items: center;
        justify-content: center;
        .x-img {
            display: block;
            width: 100%;
            padding-top: 100%;
            position: relative;
        }
        img.image-cd {
            top: 0;
            position: absolute;
            display: block;
            width: 100%;
            height: auto;
            border-radius: 50%;
            box-sizing: border-box;
            border: 10px solid rgba(0, 0, 0, .2);
            z-index: 100;
        }
    }
    .play-title {
        color: #fff;
        text-align: center;
        padding-top: 20px;
        .na {
            font-size: 18px;
            padding-bottom: 5px;
        }
    }
    .play-progress {
        padding: 0 20px 20px;
        display: flex;
        align-items: center;
        color: #fff;
        .progress-bar {
            flex: 1;
            height: 5px;
            margin: 0 20px;
            background: rgba(0, 0, 0, .2);
            i {
                background: #f00;
                width: 50%;
                display: block;
                height: 5px;
                position: relative;
                &:after {
                    content: '';
                    position: absolute;
                    background: #f00;
                    border-radius: 50%;
                    width: 18px;
                    height: 18px;
                    margin-left: -9px;
                    right: 0;
                    top: -6px;
                }
            }
        }
    }
    .play-control {
        padding: 0 50px 50px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        span {
            display: block;
            border: 1px solid #fff;
            border-radius: 50%;
        }
        .btn-prev {
            width: 40px;
            height: 40px;
        }
        .btn-play {
            width: 60px;
            height: 60px;
        }
        .btn-next {
            width: 40px;
            height: 40px;
        }
    }
}
.music-list {
    padding-bottom: 20px;
    .music-item {
        margin-bottom: 20px;
        padding: 0 20px;
        display: flex;
        align-items: center;
        position: relative;
        .l-img {
            background: #f00 no-repeat center;
            background-size: 80px;
            width: 80px;
            height: 80px;
            border-radius: 50%;
        }
        .l-s-img {
            background: #f00 no-repeat center;
            background-size: 80px;
            width: 60px;
            height: 60px;
            position: absolute;
            border-radius: 50%;
            left: 30px;
        }
        .r-text {
            margin-left: -40px;
            padding: 20px 0 20px 50px;
            flex: 1;
            border-radius: 0 10px 10px 0;
            background: #fff;
            box-shadow: 0 0 20px rgba(0, 0, 0, .09);
            margin-right: 10px;
            .play-btn {
                position: absolute;
                bottom: 20px;
                background: #f00;
                color: #fff;
                border-radius: 20px;
                padding: 2px 15px;
                right: 15px;
                box-shadow: 0 0 10px rgba(255, 0, 0, .3);
            }
        }
    }
}
.amimate-close {
    position: absolute;
    top: 10px;
    left: 10px;
    z-index: 50;
    a {
        display: inline-block;
        color: #fff;
        &:before {
            content: '';
            height: 10px;
            display: inline-block;
            vertical-align: baseline;
            width: 10px;
            border-top: 1px solid #fff;
            border-left: 1px solid #fff;
            transform: rotate(-45deg);
        }
    }
}
.amimate-view {
    height: 100%;
    width: 100%;
    .amimate-item {
        position: absolute;
        width: 100%;
        height: 100%;
        background: #000;
        overflow: hidden;
        .am-content {
            background: #fff;
            transform-origin: right;
            box-sizing: border-box;
            width: 100%;
            height: 100%;
        }
    }
}
</style>



